export default {
	data() {
		return {
			userObj:{},
			birthday:'',
			sex:''
		};
	},
	onShow(){
		this.userObj=uni.getStorageSync('userLogin')
		this.birthday=this.userObj.birthday
		this.sex=this.userObj.sex
	},
	methods:{
		clearStorage(){
			uni.clearStorage()
			uni.showLoading({
				title:'退出中'
			})
			setTimeout(function () {
			    uni.hideLoading();
				uni.navigateBack({
					delta:1
				})
			}, 1500);
		},
		operator(){
			let that=this
			uni.showActionSheet({
				itemList:['查看我的头像','从相册中选择上传'],
				success(e) {
					let index=e.tapIndex
					//如果点击了查看头像
					if(index==0){
						let imgArr=[];
						imgArr.push(that.userObj.avatarUrl)
						uni.previewImage({
							urls:imgArr,
							current:imgArr[0]
						})
					}else if(index==1){
						// uni.chooseImage({
						// 	count:1,
						// 	sizeType:['compressed'],
						// 	sourceType:['album','camera'],
						// 	success(e) {
						// 		let temp=e.tempFilePaths[0];
						// 		uni.navigateTo({
						// 			url:'../cropper/cropper?temp='+temp
						// 		})
						// 	}
						// })
						uni.redirectTo({
							url:'../cropper/cropper'
						})
					}
				}
			})
		},
		//上传返回图片
		myUpload(rsp) {
		  const self = this;
		  self.imgurl = rsp.path; //更新头像方式一
		  // rsp.avatar.imgSrc = rsp.path; //更新头像方式二
		},
		//修改昵称
		changeNickname(){
			uni.navigateTo({
				url:'../changeNickname/changeNickname'
			})
		},
		//修改生日
		changeBirthday(){
			uni.navigateTo({
				url:'../changeBirthday/changeBirthday'
			})
		},
		//修改性别
		changeSex(){
			uni.navigateTo({
				url:'../changeSex/changeSex'
			})
		}
	}

}
